.section2 {
    width: 100%;
    display: flex;
    height: 25rem;

    margin-top: 3rem;

    .btn-image {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 25rem;
        height: 25rem;
       
        background-image: url("../img/adele.webp");
        background-repeat: no-repeat;
        background-size: contain;

        button {

            background: none;
            border: none;
            cursor: pointer;
        }
    }
}

svg:hover {
    height: 5rem;   
}

.contenedor-info-artist {
    background-image: url("../img/fondo.webp");
    background-size: cover;
    background-repeat: repeat;
    width: 64.2rem;
}

.img-fondo {
    
    background-color: rgba(167, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    padding: 4rem 2.1rem 2rem 3rem;
    
}

.info-artist {
    width: 100%;
    color: white;

    h3 {
        font-size: 2.2rem;
        font-weight: 700;
        text-align: left;
    }

    .extra {
        display: flex;
        align-items: center;
        
        p {
            margin: 0;
        }

        p:first-child {
            font-size: 1.4rem;
        }

        p:last-child {
            font-size: 1rem;
            color: #662323;
            margin-left: 1rem;
        }
    }

    p {
        font-size: 1.2rem;
        line-height: 2rem;
    }

    .botones-info {
        display: flex;
        align-items: center;
      

        button {
            background-color: #E86060;
            color: white;
            border: none;
            height: 3.1rem;
            width: 11.7rem;
            border-radius: 10rem;
            font-size: 1.4rem;
            font-weight: 400;
            margin-right: 1.5rem;
            cursor: pointer;
        }

        button:nth-child(2) {
            background-color: transparent;
            border: 1px solid #EB5757;
            color: #E86060;
        }

        p {
            font-size: 3rem;
        }
    }


}